<template>
  <el-dropdown class="d2-mr">
    <span class="btn-text"> {{username}}</span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item @click.native="logOff"><d2-icon name="power-off"/> </el-dropdown-item>
      <el-dropdown-item><d2-icon name="user-circle-o"/> </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
// 
import Cookies from 'js-cookie'
// import { mapState, mapMutations } from 'vuex'
import { mapMutations } from 'vuex'
import { cancelLoginSystem } from '@/api/login'

export default {
  data () {
    return {
      sid: Cookies.get('__user__sid'),
      username: Cookies.get('__user__name')
    }
  },
  // computed: {
  //   ...mapState({
  //     username: state => state.d2admin.username
  //   })
  // },
  methods: {
    ...mapMutations([
      'd2adminDbRemoveByUuid'
    ]),
    _cancelLoginSystem (params) {
      cancelLoginSystem(params).then(res => {
        // 
        this.d2adminDbRemoveByUuid({
          key: 'username',
          emptyValue: ''
        })
        // cookie
        Cookies.remove('token')
        Cookies.remove('uuid')
        Cookies.remove('__user__sid')
        Cookies.remove('__user__customernumid')
        // 
        this.$router.push({
          name: 'login'
        })
      }).catch(err => {
        console.log(err)
      })
    },
    logOff () {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const params = {
          sid: this.sid
        }
        this._cancelLoginSystem(params)
      }).catch(() => {
        // 
      })
    }
  }
}
</script>



